<template>
  <div class="card" @click="detail(data.Id)">
    <div class="row g-0">
      <div class="col-4 bgimg">
        <div
          class="cover rounded-start"
          :style="'background-image:url(' + data.Picture1 + ')'"
        ></div>
      </div>
      <div class="col-7 card-right">
        <div class="card-body">
          <div class="name">
            {{ data.Name }}
          </div>
          <div class="cord-text">
            <div class="row">
              <div class="col-lg-6 col-md-5">
                舉辦縣市:
                <span style="font-weight: bold">{{ data.Region }}</span>
              </div>
              <div class="col-lg-6 col-md-7">
                活動開始日期:
                <span style="font-weight: bold"
                  >{{ data.Start.substr(0, 10) }}
                </span>
              </div>
            </div>
          </div>
          <div class="dis" v-html="disChange"></div>
        </div>
      </div>
      <div class="col-1">
        <button
          :class="[isAdd ? 'btn btn-danger btn-sm' : 'btn btn-success btn-sm']"
          @click.stop="follow(data.Id)"
          v-text="text"
        ></button>
        <note v-if="isNote" :card="card" :id="data.Id"></note>
      </div>
    </div>
  </div>
</template>
<script>
import note from "../components/note.vue";
export default {
  components: {
    note,
  },
  props: {
    data: {
      type: Object,
    },
    del: {
      type: Boolean,
    },
  },
  data() {
    return {
      disChange: "",
      isAdd: true,
      text: "加入關注",
      isNote: false,
      card: false,
    };
  },
  watch: {
    "$store.getters.windowWidth": {
      handler: function () {
        this.dis();
      },
    },
  },
  methods: {
    dis() {
      this.disChange = this.data.Description;
      this.disChange = "&emsp;&emsp;" + this.disChange;
      if (this.$store.getters.windowWidth < 480) {
        if (this.disChange.length > 30) {
          this.disChange = this.disChange.substr(0, 30) + "...";
        }
      } else if (this.$store.getters.windowWidth < 768) {
        if (this.disChange.length > 50) {
          this.disChange = this.disChange.substr(0, 50) + "...";
        }
      } else if (this.$store.getters.windowWidth < 992) {
        if (this.disChange.length > 70) {
          this.disChange = this.disChange.substr(0, 70) + "...";
        }
      } else {
        if (this.disChange.length > 130) {
          this.disChange = this.disChange.substr(0, 130) + "...";
        }
      }
    },
    follow(id) {
      if (document.cookie) {
        if (!this.del) {
          this.card = true;
          this.isNote = true;
          this.$store.dispatch("addId", id);
        } else {
          this.$store.dispatch("deleteId", id);
        }
      } else {
        this.card = true;
        this.isNote = true;
      }
      let outer = this;
      setTimeout(function () {
        outer.isNote = false;
      }, 3000);
    },
    delete() {
      if (this.del) {
        this.isAdd = false;
        this.text = "取消關注";
      }
    },
    detail(id) {
      this.$router.push("/detail/" + id);
    },
  },
  mounted() {
    this.dis();
    this.delete();
  },
};
</script>
