<template>
  <div class="modal fade" id="memberModal" ref="memberModal">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">會員登入</div>
        <div class="modal-body">
          <div>
            測試用帳號&密碼:&ensp;abc123
            <br />
            key好囉!
          </div>
          <div class="form-group mt-2">
            <label>帳號</label>
            <input
              type="text"
              class="form-control"
              v-model="account"
              @input="checkAc()"
            />
          </div>
          <span v-if="acWarning" style="color: red">*帳號需六個字以上</span>
          <div class="form-group mt-2">
            <label>密碼</label>
            <input
              type="password"
              class="form-control"
              v-model="password"
              @input="checkPs()"
            />
            <span v-if="psWarning1" style="color: red"
              >*密碼須同時包含英文和數字</span
            >
          </div>
          <button
            class="btn btn-primary mt-3"
            @click="submitData()"
            :disabled="btn"
          >
            送出
          </button>
          <div v-if="error" style="color: red; line-height: 54px">
            查無此會員資料，請再試一次
          </div>
          <div v-if="success" style="color: green; line-height: 54px">
            登入成功
          </div>
        </div>
        <div class="modal-footer">
          <a href="#" @click="memberModal.hide()">晚點再登入好了</a>
        </div>
      </div>
      {{ btnM() }}
    </div>
  </div>
</template>

<script>
import { Modal } from "bootstrap";
export default {
  data() {
    return {
      memberModal: null,
      account: "abc123",
      acWarning: false,
      password: "abc123",
      psWarning1: false,
      btn: true,
      error: false,
      success: false,
    };
  },
  mounted() {
    this.memberModal = new Modal(this.$refs.memberModal);
    this.memberModal.show();
  },
  methods: {
    checkAc() {
      if (this.account.length > 0 && this.account.length < 6) {
        this.acWarning = true;
      } else {
        this.acWarning = false;
      }
    },
    checkPs() {
      let x =
        (this.password.length > 0 && this.password.search(/[a-zA-Z]/) === -1) ||
        (this.password.length > 0 && this.password.search(/[0-9]/) === -1);

      if (x) {
        this.psWarning1 = true;
      } else {
        this.psWarning1 = false;
      }
    },
    btnM() {
      if (this.account != "" && this.password != "") {
        if (this.acWarning == false && this.psWarning1 == false) {
          this.btn = false;
        } else {
          this.btn = true;
        }
      }
    },
    submitData() {
      if (this.account != "abc123" || this.password != "abc123") {
        this.error = true;
      } else {
        let modal = this;
        this.error = false;
        this.success = true;
        setTimeout(() => {
          modal.memberModal.hide();
        }, 1000);
        let d = new Date();
        d.setTime(d.getTime() + 7 * 24 * 60 * 60 * 1000);
        let expire = "expire=" + d.toUTCString();
        document.cookie = "go=logIn;" + expire;
        this.$router.push("/member");
      }
    },
  },
};
</script>

<style scoped lang="scss">
.modal {
  pointer-events: none;
}
.modal-dialog {
  pointer-events: all;
}
</style>
