<template>
  <div class="container">
    <div class="row">
      <div
        ref="myWidth"
        class="col-xl-10 offset-xl-1 col-lg-12"
        style="backgroundcolor: white; height: 100vh"
      >
      <div style="display: flex;justify-content: center; ">
        <carousel ref="carousel" :showW="fixW" />
        </div>
        <navbar
          ref="navbar"
          :class="fix ? 'fix' : ''"
          :style="fix ? 'width:' + fixW + 'px' : ''"
        />
        <open-modal
          v-if="logInShow1"
          @next2="next3($event)"
          @bgColor="backgroundColor($event)"
        >
        </open-modal>
        <member-modal v-if="logInShow2"></member-modal>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import openModal from "./components/openModal.vue";
import memberModal from "./components/memberModal.vue";
import carousel from "./components/carousel.vue";
import navbar from "./components/navbar.vue";
export default {
  components: {
    openModal,
    memberModal,
    carousel,
    navbar,
  },
  data() {
    return {
      chosed: "white",
      bgcolor: "",
      logInShow1: true,
      logInShow2: false,
      src: null,
      dataWithPhoto: [],
      fix: false,
      w: 0,
      fixW: 0,
    };
  },
  methods: {
    backgroundColor(c) {
      $("#app").attr("class", c);
    },
    next3(bool) {
      this.logInShow1 = bool;
      let app = this;
      if (!this.logInShow1) {
        setTimeout(() => {
          if (document.cookie) {
            app.logInShow2 = false;
          } else {
            app.logInShow2 = true;
          }
        }, 400);
      }
    },
    scroll() {
      if (
        document.documentElement.scrollTop >
        this.$refs.carousel.$el.offsetHeight
      ) {
        this.fix = true;
      } else {
        this.fix = false;
      }
    },
    fixWidth() {
      this.w = document.body.clientWidth;
      this.$store.dispatch("windowWidth", this.w);
    },
  },
  watch: {
    $route: {
      handler: function () {
        this.$store.dispatch("getAxios");
      },
      deep: true,
      immediate: false,
    },
    "$store.getters.windowWidth": {
      handler: function () {
        this.fixW = this.$refs.myWidth.offsetWidth;
      },
      deep: true,
      immediate: false,
    },
  },
  mounted() {
    window.onscroll = this.scroll;
    this.fixWidth();
    window.onresize = this.fixWidth;
  },
};
</script>

<style lang="scss">
* {
  margin: 0px;
  padding: 0px;
  border: 0px;
}
@mixin keyframe($name, $color) {
  height: 100vh;
  animation: $name;
  animation-duration: 0s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  @at-root {
    @keyframes #{$name} {
      0% {
        background-image: linear-gradient(to bottom, white, white);
      }
      100% {
        background-image: linear-gradient(to bottom, $color, white);
      }
    }
  }
}
.gray {
  @include keyframe(gray-gradual, rgba(128, 128, 128, 0.5));
}
.yellow {
  @include keyframe(yellow-gradual, rgba(255, 255, 0, 0.5));
}
.blue {
  @include keyframe(blue-gradual, rgba(111, 111, 247, 0.5));
}
.white {
  @include keyframe(white-gradual, white);
}
.fix {
  position: fixed;
  top: 0px;
  z-index: 100;
  background-color: white;
}
</style>
