<template>
  <div>
    abc123 你好! <span class="title">以下是您的關注活動 </span
    ><button class="logOut btn btn-danger" @click="logOut()">登出</button>
  </div>
  <div style="padding-inline: 20px">
    *因無後端，<span class="note">重新整理</span>和<span class="note">
      關閉瀏覽器</span
    >紀錄即消失，登出再登入紀錄可保留，感謝您的測試~
  </div>
  <div v-if="nothing" class="title mt-5" style="padding-inline: 20px">
    您還沒有關注的活動，來用<span class="note">&ensp;日期&ensp;</span>和<span
      class="note"
      >&ensp;多地區列表&ensp;</span
    >
    尋找想關注的活動吧!
  </div>
  <div v-if="!nothing">
    <card :data="data" :del="true" v-for="data in show" :key="data.Id"></card>
  </div>
</template>

<script>
import card from "../components/card.vue";
export default {
  components: {
    card,
  },
  data() {
    return {
      list: [],
      show: [],
      nothing: true,
    };
  },
  beforeRouteEnter(to, from, next) {
    const enter = document.cookie;
    if (enter) {
      next();
    } else {
      next((vm) => {
        vm.$router.push("/latest");
      });
    }
  },
  watch: {
    "$store.getters.followId": {
      handler: function () {
        this.showUp();
      },
      deep: true,
      immediate: false,
    },
  },
  methods: {
    logOut() {
      document.cookie = "go=; expires=Fri, 01 Jan 2021 00:00:00 UTC";
      this.$store.dispatch("logOut", true);
      this.$router.push("/");
    },
    showUp() {
      this.show = [];
      this.list = [];
      let outer = this;
      this.list = this.list.concat(outer.$store.getters.followId);
      let select = this.$store.getters.dataWithPhoto.filter(function (data) {
        let y = outer.list.some(function (a) {
          return a == data.Id;
        });
        return y;
      });
      this.list.reverse();
      this.list.forEach(function (data) {
        let get = select.filter(function (item) {
          return item.Id == data;
        });
        outer.show = outer.show.concat(get);
      });
    },
  },
  mounted() {
    if (this.$store.getters.followId[0] == null) {
      this.nothing = true;
    } else {
      this.nothing = false;
      this.showUp();
    }
  },
};
</script>
<style scoped lang="scss">
.title {
  font-size: 1.6rem;
  font-weight: bold;
  color: brown;
}
.note {
  color: red;
}
.logOut {
  margin-left: 60vw;
  height: 75px;
  width: 75px;
  border-radius: 50%;
  opacity: 0.8;
}
</style>
