import { createStore } from "vuex";
import axios from "axios";
axios.defaults.baseURL = "20210727_from_opendata.json";

export default createStore({
  state: {
    followId: [],
    dataWithPhoto: null,
    logOut: false,
    repeat: false,
    windowWidth: 0,
  },
  mutations: {
    setGetAxios(state, dataWithPhoto) {
      state.dataWithPhoto = dataWithPhoto;
    },
    setAddId(state, id) {
      state.repeat = false;
      state.repeat = state.followId.some(function (item) {
        return item == id;
      });
      state.followId = state.followId.filter(function (data) {
        if (data != id) {
          return true;
        }
      });
      state.followId.push(id);
    },
    setDeleteId(state, id) {
      let index = state.followId.indexOf(id);
      state.followId.splice(index, 1);
    },
    setLogOut(state, bool) {
      state.logOut = bool;
    },
    setWindowWidth(state, num) {
      state.windowWidth = num;
    },
  },
  actions: {
    getAxios({ commit }) {
      let data = axios.get("");
      data.then((response) => {
        let src = response.data.XML_Head.Infos;
        let dataWithPhoto = src.Info.filter(function (value) {
          return value.Picture1 != "";
        });
        commit("setGetAxios", dataWithPhoto);
      });
    },
    addId({ commit }, id) {
      commit("setAddId", id);
    },
    deleteId({ commit }, id) {
      commit("setDeleteId", id);
    },
    logOut({ commit }, bool) {
      commit("setLogOut", bool);
    },
    windowWidth({ commit }, num) {
      commit("setWindowWidth", num);
    },
  },
  getters: {
    dataWithPhoto: (state) => {
      return state.dataWithPhoto;
    },
    followId: (state) => {
      return state.followId;
    },
    logOut: (state) => {
      return state.logOut;
    },
    repeat: (state) => {
      return state.repeat;
    },
    windowWidth: (state) => {
      return state.windowWidth;
    },
  },
});
