<template>
  <div
    class="modal fade"
    ref="openModal"
    data-keyboard="false"
    data-backdrop="static"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">來選擇喜歡的背景吧!</div>
        <div class="modal-body">
          <ul class="background">
            <li v-for="(color, key) in backgroundColorList" :key="color">
              <input type="radio" :id="color" :value="color" v-model="picked" />
              <label :for="color">{{ key }}</label>
            </li>
          </ul>
        </div>
        <div class="modal-footer">
          <button
            :disabled="btn"
            class="btn btn-primary"
            @click="openModalHide()"
          >
            選好了
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Modal } from "bootstrap";
export default {
  data() {
    return {
      backgroundColorList: {
        灰: "gray",
        黃: "yellow",
        藍: "blue",
        白: "white",
      },
      picked: "",
      openModal: null,
      btn: true,
    };
  },
  methods: {
    openModalHide() {
      this.openModal.hide();
      this.$emit("next2", false);
    },
  },
  watch: {
    picked(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.btn = false;
        this.$emit("bgColor", newVal);
      }
    },
  },
  mounted() {
    this.openModal = new Modal(this.$refs.openModal);
    this.openModal.show();
  },
};
</script>
<style scoped lang="scss">
ul.background {
  display: flex;
  list-style-type: none;
  li {
    flex: 1;
  }
}
.modal {
  pointer-events: none;
}
.modal-dialog {
  pointer-events: all;
}
</style>
