<template>
  <member-modal v-if="modal" ref="member"></member-modal>
  <div class="my-2">
    <button class="btn btn-success" @click="test2(2021)" ref="2021">
      2021 年
    </button>
    <button class="btn btn-success mx-2" @click="test2(2020)">2020年</button>
    <button class="btn btn-success mx-2" @click="test2(2019)">2019年</button>
    <ul class="nav nav-pills mt-2" style="font-size: 12px">
      <li class="nav-item">
        <button class="nav-link active" ref="btn" @click="test3('y')">
          整年
        </button>
      </li>
      <li v-for="(n, i) in 12" :key="i" class="nav-item">
        <button class="nav-link" :ref="'btn' + i" @click="test3(i)">
          {{ i + 1 }}月
        </button>
      </li>
    </ul>
    <card :data="data" v-for="data in show" :key="data.Id"></card>
  </div>
</template>
<script>
import { Tab } from "bootstrap";
import card from "../components/card.vue";
import memberModal from "../components/memberModal.vue";
export default {
  components: {
    card,
    memberModal,
  },
  data() {
    return {
      dataWithPhoto: [],
      year2021: [],
      year2020: [],
      before2020: [],
      month: Array(12),
      fullYear: [],
      tab: null,
      show: [],
      modal: false,
    };
  },
  beforeRouteEnter(to, from, next) {
    if (from.path == "/member") {
      if (!document.cookie) {
        next(function (vm) {
          if (vm.$store.getters.logOut) {
            vm.$store.dispatch("logOut", false);
          } else {
            vm.modal = true;
          }
        });
      } else {
        next();
      }
    } else {
      next();
    }
  },
  watch: {
    "$store.getters.dataWithPhoto": {
      handler: function () {
        (this.year2021 = []),
          (this.year2020 = []),
          (this.before2020 = []),
          (this.dataWithPhoto = this.$store.getters.dataWithPhoto);
        this.ready();
      },
      deep: true,
      immediate: false,
    },
  },
  methods: {
    ready() {
      let outer = this;
      for (let i = 0; i < outer.dataWithPhoto.length; i++) {
        let data2 = outer.dataWithPhoto[i];
        switch (data2.Start.substr(0, 4)) {
          case "2021":
            outer.year2021.push(data2);
            break;
          case "2020":
            outer.year2020.push(data2);
            break;
          default:
            outer.before2020.push(data2);
            break;
        }
      }
      outer.test2(2021);
    },
    test2(y) {
      this.tab = new Tab(this.$refs.btn);
      this.tab.show();
      let year;
      this.month = Array(12);
      this.fullYear = [];
      for (let i = 0; i < this.month.length; i++) {
        this.month[i] = [];
      }
      switch (y) {
        case 2021:
          year = this.year2021;
          break;
        case 2020:
          year = this.year2020;
          break;
        case 2019:
          year = this.before2020.filter(function (data) {
            return data.Start.substr(0, 4) === "2019";
          });
          break;
      }
      for (let i = 0; i < year.length; i++) {
        switch (year[i].Start.substr(5, 2)) {
          case "01":
            this.month[0].push(year[i]);
            break;
          case "02":
            this.month[1].push(year[i]);
            break;
          case "03":
            this.month[2].push(year[i]);
            break;
          case "04":
            this.month[3].push(year[i]);
            break;
          case "05":
            this.month[4].push(year[i]);
            break;
          case "06":
            this.month[5].push(year[i]);
            break;
          case "07":
            this.month[6].push(year[i]);
            break;
          case "08":
            this.month[7].push(year[i]);
            break;
          case "09":
            this.month[8].push(year[i]);
            break;
          case "10":
            this.month[9].push(year[i]);
            break;
          case "11":
            this.month[10].push(year[i]);
            break;
          case "12":
            this.month[11].push(year[i]);
            break;
        }
      }
      for (let i = 0; i < 12; i++) {
        this.month[i] = this.month[i].sort(function (a, b) {
          if (a.Start.substr(8, 2) > b.Start.substr(8, 2)) {
            return 1;
          } else {
            return -1;
          }
        });
        this.fullYear.push(this.month[i]);
      }
      let x = this.fullYear[0];
      for (let i = 0; i < 11; i++) {
        x = x.concat(this.fullYear[i + 1]);
      }
      this.fullYear = x;
      this.show = this.fullYear;
    },
    test3(i) {
      this.$nextTick(() => {
        if (i != "y") {
          this.tab = new Tab(this.$refs["btn" + i]);
          this.tab.show();
          this.show = this.month[i];
        } else {
          this.tab = new Tab(this.$refs.btn);
          this.tab.show();
          this.show = this.fullYear;
        }
      });
    },
  },
};
</script>
<style scoped lang="scss">
.nav-link.active {
  background-color: cornflowerblue !important;
}
</style>
