  <div class="row">
    <div class="col-lg-7 col-md-6 h-3 title">
      <sub style="color: green; font-size: 1.2rem">個人vue學習</sub>
      <br v-if="small" />
      台灣觀光資訊活動網
    </div>
    <ul class="col-lg-5 col-md-6 nav justify-content-end">
      <li class="nav-item">
        <router-link to="/latest" class="nav-link">日期列表</router-link>
      </li>
      <li class="nav-item">
        <router-link to="/region" class="nav-link">多地區列表</router-link>
      </li>
      <li class="nav-item">
        <router-link to="/member" class="nav-link">會員專區</router-link>
      </li>
    </ul>
    <div class="mark">
      此網站資料來源於政府資料開放平臺，非完整接露也非同步更新，活動內容僅供參考
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      small: false,
    };
  },
  watch: {
    "$store.getters.windowWidth": {
      handler: function () {
        if (this.$store.getters.windowWidth > 1000) {
          this.small = false;
        } else if (
          this.$store.getters.windowWidth > 768 &&
          this.$store.getters.windowWidth < 1000
        ) {
          this.small = true;
        } else if (
          this.$store.getters.windowWidth > 450 &&
          this.$store.getters.windowWidth < 768
        ) {
          this.small = false;
        } else if (this.$store.getters.windowWidth < 450) {
          this.small = true;
        }
      },
    },
  },
};
</script>
