<template>
  <div v-if="sliderData">
    <div class="carousel slide" ref="myslider" >
      <div class="carousel-inner" >
        <div
          v-for="(data, key) in sliderData"
          :key="key"
          class="carousel-item"
          :class="{ active: key == 0 }"
          @click="slider(data.Id)"
        >
          <img
            :src="data.Picture1"
            alt="picture"
            :style="'height:40vh;width:'+w+'px'"
          />
          <div class="carousel-caption">
            <div style="background-color: rgba(128, 128, 128, 0.7)">
              <h2 style="opacity: 1">{{ data.Name }}</h2>
            </div>
          </div>
        </div>
      </div>
      <a @click="prev()" class="carousel-control-prev" role="button">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
      </a>
      <a @click="next()" class="carousel-control-next" role="button">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
      </a>
    </div>
  </div>
</template>
<script>
import { Carousel } from "bootstrap";
export default {
  props: {
    showW: {
      type: Number,
    },
  },
  data() {
    return {
      carousel: null,
      sliderData: [],
      random: [],
      w: 0,
    };
  },
  methods: {
    prev() {
      this.carousel.prev();
    },
    next() {
      this.carousel.next();
    },
    slider(id) {
      this.$router.push("/detail/" + id);
    },
  },
  watch: {
    "$store.state.dataWithPhoto": {
      handler: function () {
        for (let i = 0; i < 5; i++) {
          this.random[i] = Math.floor(Math.random() * 30) + 1;
          for (let j = 0; j < i; j++) {
            if (this.random[i] == this.random[j]) {
              i = i - 1;
              break;
            }
          }
        }
        let outer = this;
        let unit;
        let arr = [];
        this.random.forEach(function (item) {
          unit = outer.$store.getters.dataWithPhoto[item];
          arr.push(unit);
        });
        this.sliderData = arr;
      },
    },
    showW: {
      handler: function () {
        if(this.showW>1200){
         this.w = this.showW*0.65;
        }else if((this.showW>900)){
          this.w = this.showW*0.80;
        }else{
          this.w=this.showW;
        }
      },
    },
  },
  mounted() {
    this.carousel = new Carousel(this.$refs.myslider);
  },
};
</script>
<style scoped lang="scss">
.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 4rem !important;
  width: 3rem !important;
  outline: black;
  border-radius: 50%;
  border: 3px solid black;
  background-color: rgba(255, 0, 0, 0.5);
}
</style>
