<template>
  <div v-if="data">
    <div class="title">{{ data.Name }}</div>
    <table class="table">
      <tr class="tr1">
        <td class="td1"><span class="note">開始日期:</span></td>
        <td class="td2">{{ data.Start.substr(0, 10) }}</td>
        <td class="td1"><span class="note">結束日期:</span></td>
        <td>{{ data.Start.substr(0, 10) }}</td>
      </tr>
      <tr class="tr2">
        <td class="note" style="width:min(70px,30vw)">地點:</td>
        <td>{{ data.Location }}</td>
        <td class="td1 note">洽詢單位:</td>
        <td>{{ data.Org }}</td>
      </tr>
      <tr v-if="data.Website">
        <td><span class="note td1">網址:</span></td>
        <td colspan="3">
          <a style="padding: 0rem 0rem" :href="data.Website" target="_blank">{{
            data.Website
          }}</a>
        </td>
      </tr>
    </table>
    <div v-if="cut" v-text="dis1"></div>
    <img class="pic1" :src="data.Picture1" alt="" />
    <div v-text="dis2" style="margin-top: 1rem"></div>
    <br />
    <img class="pic2" :src="data.Picture2" alt="" />
    <div style="text-align: center" v-if="data.Picture2 != ''">
      圖片描述: &ensp;{{ data.Picdescribe2 }}
    </div>
    <img class="pic2" :src="data.Picture3" alt="" />
    <div style="text-align: center" v-if="data.Picture3 != ''">
      圖片描述: &ensp;{{ data.Picdescribe3 }}
    </div>
    <button class="follow btn btn-danger" @click="follow(data.Id)">
      加入關注
    </button>
    <note v-if="isShow" :detail="true" :id="data.Id"></note>
  </div>
</template>

<script>
import note from "../components/note.vue";
export default {
  components: {
    note,
  },
  data() {
    return {
      data: null,
      isShow: false,
      cut: false,
      dis1: "",
      dis2: "",
    };
  },
  beforeRouteEnter(to, from, next) {
    if (from.path == "/latest" || from.path == "/region") {
      next(function (vm) {
        vm.getData();
      });
    } else {
      next(function (vm) {
        vm.$store.dispatch("getAxios");
        vm.$watch("$store.getters.dataWithPhoto", function () {
          let from = vm.$store.getters.dataWithPhoto;
          let show = from.filter(function (item) {
            return vm.$route.params.id == item.Id;
          });
          vm.data = show[0];
          vm.showDis();
        });
      });
    }
  },
  watch: {
    "$store.getters.windowWidth": {
      handler: function () {
        if (this.data != null) {
          this.showDis();
        }
      },
      immediate: false,
    },
  },
  methods: {
    getData() {
      let x = this.$store.getters.dataWithPhoto;
      let outer = this;
      x = x.filter(function (data) {
        return data.Id == outer.$route.params.id;
      });
      this.data = x[0];
      this.showDis();
    },
    showDis() {
      if (this.$store.getters.windowWidth < 1280) {
        this.cut = true;
        this.dis1 = this.data.Description.substr(0, 140);
        let some = this.data.Description.indexOf(",", 140);
        some = some + 140;
        this.dis1 = this.data.Description.substr(0, some + 2);
        this.dis2 = this.data.Description.slice(some + 2, -1);
      } else {
        this.cut = false;
        this.dis2 = this.data.Description;
      }
    },
    follow(id) {
      this.isShow = true;
      if (document.cookie) {
        this.$store.dispatch("addId", id);
      }
      let outer = this;
      setTimeout(function () {
        outer.isShow = false;
      }, 3000);
    },
  },
};
</script>

