<template>
  <div v-if="mark">
    <div :class="final" v-text="mark"></div>
  </div>
</template>
<script>
export default {
  props: {
    card: {
      type: Boolean,
    },
    id: {
      type: String,
    },
    detail: {
      type: Boolean,
    },
  },
  data() {
    return {
      mark: null,
      effective: false,
      final: null,
    };
  },
  methods: {
    setType() {
      this.final = "";
      if (this.card) {
        if (document.cookie) {
          if (this.$store.getters.repeat) {
            this.final = "badge bg-danger note1";
          } else {
            this.final = "badge bg-success note1";
          }
        } else {
          this.final = "badge bg-warning text-dark note1";
        }
      }

      if (this.detail) {
        if (document.cookie) {
          if (this.$store.getters.repeat) {
            this.final = "badge bg-danger note2";
          } else {
            this.final = "badge bg-success note2";
          }
        } else {
          this.final = "badge bg-warning text-dark note2";
        }
      }
    },
  },
  mounted() {
    if (document.cookie) {
      if (this.$store.getters.repeat) {
        this.mark = "已經加入過囉";
      } else {
        this.mark = "加入成功!";
      }
    } else {
      this.mark = "請先從會員專區登入喔";
    }
    this.setType();
  },
};
</script>

