<template>
  <div class="mt-2">
    <span style="font-size: 0.9rem">北部地區>></span>
    <div class="btn-group btn-group-sm" role="group">
      <span v-for="city in north" :key="city.EN">
        <input
          type="checkbox"
          class="btn-check"
          :value="city.TC"
          :id="city.EN"
          v-model="checked"
        />
        <label class="btn btn-outline-primary" :for="city.EN">{{
          city.TC
        }}</label>
      </span>
    </div>
    <div class="mt-1">
      <span style="font-size: 0.9rem">中部地區>></span>
      <div class="btn-group btn-group-sm" role="group">
        <span v-for="city in center" :key="city.EN">
          <input
            type="checkbox"
            class="btn-check"
            :value="city.TC"
            :id="city.EN"
            v-model="checked"
          />
          <label class="btn btn-outline-primary" :for="city.EN">{{
            city.TC
          }}</label>
        </span>
      </div>
      <div class="mt-1">
        <span style="font-size: 0.9rem">南部地區>></span>
        <div class="btn-group btn-group-sm" role="group">
          <span v-for="city in south" :key="city.EN">
            <input
              type="checkbox"
              class="btn-check"
              :value="city.TC"
              :id="city.EN"
              v-model="checked"
            />
            <label class="btn btn-outline-primary" :for="city.EN">{{
              city.TC
            }}</label>
          </span>
        </div>
      </div>
      <div class="mt-1">
        <span style="font-size: 0.9rem">東部地區 &其他>></span>
        <div class="btn-group btn-group-sm" role="group">
          <span v-for="city in east" :key="city.EN">
            <input
              type="checkbox"
              class="btn-check"
              :value="city.TC"
              :id="city.EN"
              v-model="checked"
            />
            <label class="btn btn-outline-primary" :for="city.EN">{{
              city.TC
            }}</label>
          </span>
        </div>
        <input
          type="checkbox"
          class="btn-check"
          style="margin-left: -20px"
          value="其它"
          id="other"
          v-model="checked"
        />
        <label class="btn btn-outline-primary" for="other">其它</label>
      </div>
      <div style="color: red">排序:活動開始日期 最新-->最舊</div>
    </div>
    <div>
      <card :data="data" v-for="data in show" :key="data.Id"></card>
    </div>
  </div>
</template>
<script>
import card from "../components/card.vue";
export default {
  components: {
    card,
  },
  data() {
    return {
      dataWithPhoto: null,
      Taipei: [],
      other: [],
      show: [],
      checked: ["臺北市"],
      north: [
        { EN: "Keelung", TC: "基隆市" },
        { EN: "Taipei", TC: "臺北市" },
        { EN: "newTaipei", TC: "新北市" },
        { EN: "Taoyuan", TC: "桃園市" },
        { EN: "Hsinchu", TC: "新竹縣市" },
        { EN: "Miaoli", TC: "苗栗縣" },
      ],
      center: [
        { EN: "Taichung", TC: "臺中市" },
        { EN: "Changhua", TC: "彰化縣" },
        { EN: "Yunlin", TC: "雲林縣" },
        { EN: "Nantou", TC: "南投縣" },
      ],
      south: [
        { EN: "Chiayi", TC: "嘉義縣市" },
        { EN: "Tainan", TC: "臺南市" },
        { EN: "Kaohsiung", TC: "高雄市" },
        { EN: "Pingtung", TC: "屏東縣" },
      ],
      east: [
        { EN: "Yilan", TC: "宜蘭縣" },
        { EN: "Hualien", TC: "花蓮縣" },
        { EN: "Taitung", TC: "臺東縣" },
      ],
    };
  },
  watch: {
    checked: {
      handler: function () {
        this.order();
      },
      deep: true,
    },
    "$store.getters.dataWithPhoto": {
      handler: function () {
        this.dataWithPhoto = this.$store.getters.dataWithPhoto;
        this.ready();
      },
      deep: true,
      immediate: false,
    },
  },
  methods: {
    ready() {
      for (let i = 0; i < this.dataWithPhoto.length; i++) {
        let data2 = this.dataWithPhoto[i];
        switch (data2.Region) {
          case "臺北市":
            this.show.push(data2);
            break;
        }
      }
      let all = [];
      this.north.forEach(function (data) {
        all.push(data.TC);
      });
      this.center.forEach(function (data) {
        all.push(data.TC);
      });
      this.south.forEach(function (data) {
        all.push(data.TC);
      });
      this.east.forEach(function (data) {
        all.push(data.TC);
      });
      all.splice(4, 1, "新竹市");
      all.splice(4, 0, "新竹縣");
      all.splice(11, 1, "嘉義市");
      all.splice(11, 0, "嘉義縣");

      this.other = this.dataWithPhoto.filter(function (data) {
        let y = all.some(function (a) {
          return a == data.Region;
        });
        return !y;
      });
    },
    order() {
      this.show = [];
      let outer = this;

      for (let i = 0; i < this.checked.length; i++) {
        if (outer.checked[i] == "其它") {
          this.show = this.show.concat(this.other);
        }
        let select = this.dataWithPhoto.filter(function (data) {
          if (data.Region == "新竹市" || data.Region == "新竹縣") {
            if (outer.checked[i] == "新竹縣市") {
              return true;
            }
          }
          if (data.Region == "嘉義市" || data.Region == "嘉義縣") {
            if (outer.checked[i] == "嘉義縣市") {
              return true;
            }
          }
          return data.Region == outer.checked[i];
        });
        this.show = this.show.concat(select);
      }
      for (let i = 0; i < this.show.length; i++) {
        let date = this.show[i].Start.substr(0, 10);
        date = date.replaceAll("-", "");
        this.show[i].Start = date;
      }
      this.show = this.show.filter(function (data) {
        return data.Start > 20190000;
      });
      this.show = this.show.sort(function (a, b) {
        if (a.Start > b.Start) {
          return -1;
        } else {
          return 1;
        }
      });
      for (let i = 0; i < this.show.length; i++) {
        let year = this.show[i].Start.substr(0, 4);
        let month = this.show[i].Start.substr(4, 2);
        let day = this.show[i].Start.substr(6, 2);
        this.show[i].Start = year + "-" + month + "-" + day;
      }
    },
  },
};
</script>
<style scoped lang="scss">
.btn {
  font-weight: 300px;
  line-height: 0.8;
  font-size: 0.6rem;
}
</style>
